import * as Tone from 'tone';

// Hack for audio context resumption in ios
export function startTone(){
  console.log("State" + Tone.context.state);
  const osc = Tone.context.createOscillator();
  const gain = Tone.context.createGain();
  
  gain.gain.value = 1e-37;
  // @ts-ignore
  osc.connect(gain);
  osc.start();

  //   //@ts-ignore
  //   if (Tone.context.state === 'interrupted' || 
  //   Tone.context.state === 'suspended') {
  //     //@ts-ignore
  //     return Tone.context._context.resume();
  // }

  return Promise.resolve();
}